}
ul.shift input[type="radio"],
ul.shift label {
  transition: all 0.6s ease;
  box-sizing: border-box;
}
ul.shift input[type="radio"] + label:before {
  content: "\A0";
  display: inline-block;
  vertical-align: middle;
  margin-right: 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 1px solid #292d35;
  padding: 2px;
}
